@import url(https://fonts.googleapis.com/css?family=Patua+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #527787;
}

body {
  color: #2d302f;
  
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  --international-orange: #b2696d;
  --medium-blue: #527787;
  --light-blue: #75a2b3;
  --light-gray: #efefef;
}

.bg-dark {
  background-color: var(--medium-blue)!important;
  color: white;
}

.bg-med-blue {
  background-color: var(--light-blue);
}

.bg-light {
  background-color: var(--light-gray)!important;
}

.header-text {
  padding-top: 4rem;
  padding-right: 10vw;
}
.parallax {
  background-image: url(/static/media/header.7c1f567b.png);

  height: 50vh;

  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.parallax-top {
  background-position: top;
}
.parallax-bottom {
  background-position: bottom;
}

.navbar-brand img {
  width: 40px;
  margin-left: 10px;
}

.section-img {
  margin-bottom: 10px;
  color: var(--international-orange);
}

.section-title {
  text-transform: lowercase;
}

h1, h2, h3, h4 {
  font-family: 'Patua One', cursive;
  color: var(--international-orange);
}
h5 {
  font-weight: 600;
}

.container-fluid a {
  color: var(--medium-blue);
  text-decoration: underline;
}
a:hover {
  color: var(--light-blue);
}

.text-xl {
  font-size: 10em;
}
.nav-link {
    margin-left: 9px;
    font-weight: 500;
}

.navbar-dark {
    background-color: #527787;
}

.navbar-brand {
    margin-right: 10px;
}
.navbar-brand img {
    height: 25px;
    width: 38px;
    margin-top: 8px;
    margin-bottom: 8px;
}

.navbar-icon {
    height: 28px;
    margin-top: 2px;
}
.footer {
    width: 100%;
    margin: 0;
    background-color: #527787;
    font-size: 0.8rem;
    color: white;
}
.badge {
    margin-right: 5px;
    padding: 5px 10px;
    font-size: .80rem;
}
.badge a {
    color: white;
}
.badge a:hover {
    color: white;
}
.badge-primary {
    background-color: var(--medium-blue)!important;
}

.badge-secondary {
    background-color: var(--light-blue)!important;
}
.card-header {
    background-color: #efefef;
}
.card-header button {
    color: #2d302f;
    font-weight: 600;
}
.card-header button:hover {
    color: #527787;
}
.card-body ul {
    margin-bottom: 0;
}
.work-container {
    position: relative;
    text-align: center;
    margin: 0px 5px 10px 5px;
}

.medium {
    /* visibility: hidden; */
    position: absolute;
    width: 100%;
    height: 20px;
    bottom: 0;
    padding-top: 9px;
    color: white;
    background-color: rgba(0, 0, 0, 0.4);
    font-size: x-small;
}

#artworks {
    /* Prevent vertical gaps */
    line-height: 0;
        
    -webkit-column-count: 4;
    -webkit-column-gap:   0px;
    -moz-column-count:    4;
    -moz-column-gap:      0px;
    column-count:         4;
    grid-column-gap:           0px;
    column-gap:           0px;  
}

#artworks img {
    width: 100% !important;
    height: auto !important;
}

@media (max-width: 1200px) {
    #artworks {
    -moz-column-count:    4;
    -webkit-column-count: 4;
    column-count:         4;
    }
}
@media (max-width: 1000px) {
    #artworks {
    -moz-column-count:    3;
    -webkit-column-count: 3;
    column-count:         3;
    }
}
@media (max-width: 800px) {
    #artworks {
    -moz-column-count:    2;
    -webkit-column-count: 2;
    column-count:         2;
    }
}
@media (max-width: 400px) {
    #artworks {
    -moz-column-count:    1;
    -webkit-column-count: 1;
    column-count:         1;
    }
}
